@media only screen and (max-width : 480px) {
  .row{
    margin-left: 0;
    margin-right: 0;
  }
  .blog-post-single .post-content {
    padding-top: 10px;
    width: 100%;
    float: left;
    margin-bottom: 20px;
  }
  .blog-post-single {
    margin-bottom: 80px;
    width: 100%;
    float: left;
  }
}
@media only screen and (max-width : 767px) {
  .blog-post-single .post-content h3,
  .blog-newsletter .inside-sub h3{
    font-size: 24px;
  }
  .blog-newsletter .inside-sub p{
    font-size: 14px;
  }
  .blog-post-single .post-content p{
    font-size: 14px;
  }
  .footer-widget .fwidget-link h3, 
  .footer-widget .fwidget-latest h3{
    font-size: 20px;
  }
  .fwidget-latest,
  .fwidget-link{
    margin-top: 50px;
  }
  .blog-post-single .date{
    left: 0;
    top: 0;
    padding: 6px 2px;
  }
}


@media only screen and (min-device-width : 768px) and (max-device-width : 992px) {
  .blog-post-single .post-content h3,
  .blog-newsletter .inside-sub h3{
    font-size: 24px;
  }
  .blog-newsletter .inside-sub p{
    font-size: 14px;
  }
  .blog-post-single .post-content p{
    font-size: 14px;
  }
  .footer-widget .fwidget-link h3, 
  .footer-widget .fwidget-latest h3{
    font-size: 20px;
  }

  .blog-post-single .post-content {
    width: 100%;
    float: left;
  }
  .blog-post-single {
    float: left;
  }
}


