.navbar-brand.home8{
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  max-width: 200px;
}

button.close {
  outline: 0;
  background-color: white;
  border: 0;
  padding: 22px;
  z-index: 10;
}

  /* Icon */
  .bar {position: relative;}

    .bar:before, 
    .bar:after {
    content: "";
    position: absolute;
    }

    .bar:before, 
    .bar:after, 
    .bar {
    width: 24px;
    height: 3px;
    background: black;
    margin: 3px 0;
    display: block;
    }

    .bar:before { bottom: 100%; } 
    .bar:after { top: 100%; } 
    .bar.active:after { top: 0; } 
    .bar.active:before { bottom: -200%; } 
    .bar.active { background-color: transparent; } 
    .bar, .bar:before, .bar:after { transition: all .25s linear !important; } 
    .bar.active:before,  .bar.active:after { transition-delay: .15s !important; } 
    .bar.active:before { transform: rotate(45deg); } 
    .bar.active:after { transform: rotate(-45deg); } 

.outside {
width: 25%;
min-width: 200px;
max-width: 250px;
height: 100%;
transform: translateX(-101%);
transition: all .5s cubic-bezier(0,0.05,0,1.3);
overflow: hidden;
position: absolute;
left: 0;
top: 0;
z-index: 9999;
}

  .in {
  transform: translateX(0);
  transition: 0;
  }

  .inside {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  display: block;
  width: 100%;
  height: 100%;
  background: whitesmoke;
  border-right: 1px solid rgba(255,255,255,0.2);
  border-radius: 0 100% 100% 0;
  transition: all .25s cubic-bezier(1,0.05,1,1.5);
  transition-delay: .5s;

  }

  .in .inside {
  border-radius: 0;
  transition-delay: 0s;

  }

/* material animation */
  .inside:before {
  content: "";
  width: 2100px;
  height: 2100px;
  display: block;
  background-color: white;
  position: absolute;
    top: -50%;
    left: -100%;
    z-index: -1;
  transition: all .75s linear;
  transition-delay: .15s;
  transform: scale(0);
  transform-origin: top right;
  border-radius: 1000px;}

    .outside.in .inside:before {
    transform: scale(1);

    }

/* Menu */
ul.menu {
list-style: none;
margin: 0;
padding: 0;
line-height: 2;
}

.menu li {
background: whitesmoke;
opacity: 0;
padding: 12px 22px;
border-bottom: 1px solid rgba(255,255,255,.5);
color: gray;
transition: all .25s linear;
}
.menu li a{
  color: grey;
  text-transform: capitalize;
}

.in .menu li {
transform: translateX(0%);
opacity: 1;
}


